.breed-select {
  margin: 10px;
}

.cat-info {
  padding: 10px;
}

.cat-info img {
  height: 300px;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  object-position: center;
}

.error {
  padding-left: 10px;
  display: none;
}

.spinner {
  width: 70px;
  text-align: center;
  margin: 100px auto 0;
  display: none;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #333;
  border-radius: 100%;
  animation: sk-bouncedelay 1.4s ease-in-out infinite both;
  display: inline-block;
}

.spinner .bounce1 {
  animation-delay: -.32s;
}

.spinner .bounce2 {
  animation-delay: -.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }

  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    transform: scale(0);
  }

  40% {
    transform: scale(1);
  }
}

